const ua = navigator.userAgent.toLowerCase();

export const isChrome = navigator.userAgent.indexOf('Chrome') > -1 && window.chrome;

export const getMode: () => string | undefined = () => import.meta.env?.MODE;
export const getWebEnv: () => string | undefined = () => import.meta.env?.WEB_ENV;

export const isStagingMode: boolean = import.meta.env?.WEB_ENV === 'staging';
export const isPrtMode: boolean = import.meta.env?.WEB_ENV === 'prt';
export const isOnlineMode: boolean = import.meta.env?.WEB_ENV === 'online';

export const isTestEnv: boolean = import.meta.env?.MODE === 'test';
export const isDevelopmentEnv: boolean = import.meta.env?.MODE === 'development';
export const isProductionEnv: boolean = import.meta.env?.MODE === 'production';

export const isMicro = () => !!(window as any)?.__POWERED_BY_QIANKUN__;

/**
 * 是否高级浏览器，以下浏览器都是高级浏览
 * edge浏览器、opera浏览器、chrome浏览器、firefox浏览器
 */
export const isAdvancedBrowser = (() => {
  if (/applewebkit/g.test(ua)) {
    // edge浏览器
    if (/edge/g.test(ua)) {
      return true;
    } else if (/opr/g.test(ua)) {
      // opera浏览器
      return true;
    } else if (/chrome/g.test(ua)) {
      // chrome浏览器
      return true;
    } else if (/safari/g.test(ua)) {
      // safari浏览器
      return false;
    }
  } else if (/gecko/g.test(ua) && /firefox/g.test(ua)) {
    // firefox浏览器 gecko内核
    return true;
  } else if (/presto/g.test(ua)) {
    // presto内核 opera浏览器
    return true;
  }
  return false;
})();
